.pagination {
  text-align: center;
  margin-bottom: 2rem;
}

.pagination > span {
  display: inline-block;
  margin-inline: .5rem;
  padding: .5rem;
  border: 1px solid black;
  border-radius: .25rem;
  cursor: pointer;
  transition: .3s ease;
}

.pagination > span:hover {
  background-color: #6ef36e;
}

.pagination > .active-page {
  background-color: #6ef36e;
  border-color: transparent;
}

.page-btn {
  padding: .25em;
  border: 1px solid black;
  border-radius: .25rem;
  cursor: pointer;
  transition: .3s ease;
  background-color: #6ef36e;
  font-size: 1.25rem;
}

.paginator-next-page {
  margin-left: 1rem;
}

.paginator-prev-page {
  margin-right: 1rem;
}