.friendsList {
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-top: 1rem;
  display: flex;
  column-gap: 1rem;
}

.friendsListItem{
  text-align: center;
}

.friendsLink {
  display: flex;
  flex-direction: column;
  row-gap: .5rem;
  color: #fff;
  text-decoration: none;
}

.friendsAva {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: yellow;
}