.nav {
  grid-area: nav;
  background-color: #6ef36e;
  padding: 1rem;
}

.navList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.navList a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #fff;
  transition: .3s color;
}



.navList a:hover {
  color: #1f2eb4;
}

.navList li:last-child {
  margin-top: 2rem;
}

.navList a[aria-current="page"] {
  color: gold;
}