.user {
  width: 100%;
  max-width: 70ch;
  display: flex;
  align-items: flex-start;
  column-gap: 1rem;
}

.user + .user {
  margin-top: 1rem;
}

.userInteractive {
  flex-basis: 25%;
  text-align: center;
}

.userInteractive .userAvatar {
  margin-inline: auto;
  margin-bottom: 1rem;
}

.userInfo {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: .5rem;
  color: #838383;
}

.userInfoData {
  display: flex;
  justify-content: space-between;
}

.userInfoStatus {
  margin: 0;
}

.userFollow {
  border: 0;
  background-color: #6ef36e;
  padding: .5em 1em;
  border-radius: 0.25rem;
  color: #fff;
  cursor: pointer;
  font:inherit;
}

.userFollow:disabled {
  background-color: rgba(197, 197, 197, 0.3);
  color: rgba(66, 66, 66, 0.3);
  border-color: rgba(195, 195, 195, 0.3);
  cursor: not-allowed;
}