.App {
  max-width: 1200px;
  margin: 1rem auto;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: minmax(150px, 25%) 1fr;
  gap: 1rem;
  grid-template-areas: 'header header'
                        'nav main';
}

.App-content {
  grid-area: main;
  background-color: #6ef36e;
  padding: 1rem;
}

.App-content:has(.dialogs-content, .users, .authorization) {
  background-color: #fff;
  padding: 0;
}


.userAvatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #bebebe;
}